<script lang="ts" context="module">
    export type Item = {
        name: string;
        id: string;
    };

    export type SubItem = {
        name: string;
        id: string;
        shared: boolean;
    };

    export type Filter = {
        options: string[];
        apply: (setting: string, query: Item) => boolean;
    };
</script>

<script lang="ts">
    import { slide } from "svelte/transition";

    let activeItemId: string;
    let activeSubItemId: string;

    let input: HTMLInputElement;

    export const open = (condition = true) => (input.checked = condition);

    export const setActive = (id: Item["id"], subId: SubItem["id"]) => {
        activeItemId = id;
        activeSubItemId = subId;
    };

    export const setFirstActive = () => {
        if (items.length > 0) {
            activeItemId = items[0].id;
        }
    };

    export let items: Item[];
    export let onSelectItem: ((id: Item["id"]) => void) | null = null;
    export let onSelectSubItem:
        | ((id: Item["id"], subid: SubItem["id"]) => void)
        | null = null;

    export let addSubItem: (id: Item["id"]) => any;
    export let getSubItems: (id: Item["id"]) => Promise<SubItem[]>;

    export let filter: Filter | null = null;
    export let currentFilter: string = "All";

    export let id: string = "drawer";
    export let closedText: string;
    export let addText: string;

    function selectItem(itemId: string) {
        activeItemId = itemId;
        onSelectItem?.(itemId);
    }

    function selectSubItem(id: Item["id"], subid: SubItem["id"]) {
        activeItemId = id;
        activeSubItemId = subid;
        open(false);
        onSelectSubItem?.(id, subid);
    }

    function setFilter(value: string) {
        currentFilter = value;
    }

    function handleSelectChange(event: Event) {
        const selectElement = event.target as HTMLSelectElement; // Correct casting
        if (selectElement && selectElement.value) {
            setFilter(selectElement.value);
        }
    }

    $: filteredItems = items.filter(
        (item) => currentFilter === "All" || filter?.apply(currentFilter, item),
    );

    $: subitemsPromise = activeItemId
        ? getSubItems(activeItemId)
        : Promise.resolve([]);
</script>

<div class="drawer">
    <input {id} type="checkbox" class="drawer-toggle" bind:this={input} />
    <div class="drawer-content w-fit bg-blue h-full">
        <label
            for={id}
            class="btn btn-primary rounded-none text-lg h-full rounded-r-xl border-none px-1"
            style:writing-mode="vertical-lr"
            style:color="white"
            style:background-color="#53389e"
        >
            <div class="h-full">{closedText}</div>
        </label>
    </div>
    <div class="drawer-side" style:z-index="1001">
        <label for={id} aria-label="close sidebar" class="drawer-overlay" />
        <div class="h-full flex flex-col" style:background-color="#53389e">
            <select
                on:change={handleSelectChange}
                class="select select-bordered m-2"
            >
                {#each ["All", ...(filter?.options ?? [])] as value}
                    <option {value}>{value}</option>
                {/each}
            </select>
            <div class="flex-grow overflow-scroll">
                <ul class="menu bg-base-200 w-60 custom-drawer">
                    {#each filteredItems as item}
                        {@const active = item.id === activeItemId}
                        {@const select = () => selectItem(item.id)}
                        <li class:active class="constrain">
                            <h2 class="menu-title text-white constrain">
                                <button class="btn btn-ghost" on:click={select}>
                                    {item.name}
                                </button>
                            </h2>
                            {#if active}
                                {#await subitemsPromise then subitems}
                                    <ul
                                        class="constrain"
                                        transition:slide
                                        style:padding-inline-end="0.5rem"
                                    >
                                        {#each subitems as subitem}
                                            {@const active =
                                                subitem.id === activeSubItemId}
                                            {@const select = () =>
                                                selectSubItem(
                                                    item.id,
                                                    subitem.id,
                                                )}
                                            {@const { shared, name } = subitem}
                                            <li class:active class="constrain">
                                                <button
                                                    on:click={select}
                                                    class="constrain m-0 p-1"
                                                >
                                                    <div
                                                        class="inline-block w-4 h-4"
                                                    >
                                                        {#if shared}
                                                            <img
                                                                src="/shared.png"
                                                            />
                                                        {/if}
                                                    </div>
                                                    {name}
                                                </button>
                                            </li>
                                        {/each}
                                        <center class="py-4">
                                            <button
                                                class="btn btn-sm bg-custom-green hover:bg-green-700 text-white"
                                                on:click={() =>
                                                    addSubItem(item.id)}
                                            >
                                                {addText}
                                            </button>
                                        </center>
                                    </ul>
                                {/await}
                            {/if}
                        </li>
                    {/each}
                </ul>
            </div>
        </div>
    </div>
</div>

<style>
    .constrain {
        width: 100%;
        max-width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        display: block;
    }

    .custom-drawer {
        background-color: #53389e;
        color: white;
        font-family: "Inter", sans-serif;
        font-size: 1.1rem;
    }

    .active {
        font-weight: bold;
        background-color: #9e77ed;
        border-radius: 5px;
    }

    .active > ul {
        background-color: inherit;
        margin: 0;
        list-style-type: none;
    }
    .active a,
    .active button,
    .active li {
        border: none;
        color: white;
    }

    .active li.active {
        font-weight: bold;
        background-color: rgba(255, 255, 255, 0.2);
        color: black;
    }

    li:not(.active) li {
        border: none;
    }
    li {
        margin-top: 10px;
    }
</style>
